* {
  margin: 0;
  padding: 0;
}

html {
  background: #f0f0f0;
  background-color: #f0f0f0;
  cursor: url("http://kidstudio.co/assets/css/../images/cursor.png") 25 15, auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: inherit;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  cursor: url("http://kidstudio.co/assets/css/../images/hover.png") 25 15, auto; }

/*Home page*/
.noscroll {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden; }

.home-feature,
.info-feature {
  width: 67vw;
  height: 67vh; }

.next {
  font-family: "Andale Mono", "AndaleMono", Andale-Mono, AndaleMono, monospace;
  font-size: 0.9em;
  color: #030303;
  position: fixed;
  right: 25px;
  letter-spacing: 0.1em;
  z-index: 565656;
  top: 50%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  cursor: url("http://kidstudio.co/assets/css/../images/right.png") 25 15, auto; }

.prev {
  font-family: "Andale Mono", "AndaleMono", Andale-Mono, AndaleMono, monospace;
  font-size: 0.9em;
  color: #030303;
  position: fixed;
  left: 0px;
  top: 50%;
  letter-spacing: 0.1em;
  z-index: 565656;
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  cursor: url("http://kidstudio.co/assets/css/../images/left.png") 25 15, auto; }

.black-line {
  position: fixed;
  left: 15%;
  top: 35%;
  height: 10px;
  width: 156px;
  background-color: #060606; }

#label1 {
  left: 15%;
  top: 37%;
  vertical-align: text-top; }

#label2 {
  left: 115%;
  top: 37%;
  vertical-align: text-top; }

#label3 {
  left: 215%;
  top: 37%;
  vertical-align: text-top; }

.title-home {
  position: fixed;
  display: inline-block;
  white-space: nowrap; }

.line1 {
  font-family: "Helvetica", Helvetica;
  font-size: 4em;
  color: #060606;
  font-weight: 600;
  display: inline-block; }

.img-counter {
  position: fixed;
  top: 70%;
  left: 79%;
