.blah {
  position: fixed;
  z-index: 1;
  position: absolute;
  margin-bottom: 30px;
  display: inline-block; }

.blog-post-info {
  -webkit-transform: translate(-25px, 25px);
          transform: translate(-25px, 25px);
  position: absolute;
  margin-left: -20px; }

.blog-post-title {
  font-family: "Helvetica-Neue-Bold", "Helvetica Neue Bold", "HelveticaNeueBold", Helvetica-Neue, HelveticaNeue, "Helvetica", Helvetica, "Arial", Arial, sans-serif;
  font-size: 1.8em;
  font-weight: bold; }

.post-title {
  color: black;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px; }

.blog-timestamp {
  display: inline-block;
