/*Footer*/
.footer a:hover {
  color: white;
  background-color: #030303;
  padding-bottom: 5px; }

.footer {
  font-family: "Andale Mono", "AndaleMono", Andale-Mono, AndaleMono, monospace;
  font-size: 0.8em;
  color: #030303;
  z-index: 565656;
  bottom: 4em;
  left: 0;
  position: fixed;
  width: 100%;
  letter-spacing: 0.1em;
  z-index: 565656;
  display: inline-block; }

.social-links {
  margin: 2em;
